.buttonGlowing {
  position: relative;
  display: inline-block;
  padding: 25px 70px;
  margin-top: 15px;
  margin-bottom: 40px;
  margin-left: 20;
  margin-right: 20;
  color: #03e9f4;
  text-decoration: none;
  text-transform: uppercase;
  transition: 0.5s;
  letter-spacing: 4px;
  overflow: hidden;
  /* margin-right: 50px; */
  font-family: "Orbitron";
  font-weight: 900;
  letter-spacing: 0px;
  font-size: 2em;
}
.buttonGlowing:hover {
  background: #03e9f4;
  color: #050801;
  box-shadow: 0 0 5px #03e9f4, 0 0 25px #03e9f4, 0 0 50px #03e9f4,
    0 0 200px #03e9f4;
  -webkit-box-reflect: below 1px linear-gradient(transparent, #0005);
}
/* .buttonGlowing:nth-child(1) {
  filter: hue-rotate(270deg);
}
.buttonGlowing:nth-child(2) {
  filter: hue-rotate(110deg);
} */
.buttonGlowing span {
  position: absolute;
  display: block;
}
.buttonGlowing span:nth-child(1) {
  top: 0;
  left: 0;
  width: 100%;
  height: 2px;
  background: linear-gradient(90deg, transparent, #03e9f4);
  animation: animate1 1s linear infinite;
}
@keyframes animate1 {
  0% {
    left: -100%;
  }
  50%,
  100% {
    left: 100%;
  }
}
.buttonGlowing span:nth-child(2) {
  top: -100%;
  right: 0;
  width: 2px;
  height: 100%;
  background: linear-gradient(180deg, transparent, #03e9f4);
  animation: animate2 1s linear infinite;
  animation-delay: 0.25s;
}
@keyframes animate2 {
  0% {
    top: -100%;
  }
  50%,
  100% {
    top: 100%;
  }
}
.buttonGlowing span:nth-child(3) {
  bottom: 0;
  right: 0;
  width: 100%;
  height: 2px;
  background: linear-gradient(270deg, transparent, #03e9f4);
  animation: animate3 1s linear infinite;
  animation-delay: 0.5s;
}
@keyframes animate3 {
  0% {
    right: -100%;
  }
  50%,
  100% {
    right: 100%;
  }
}

.buttonGlowing span:nth-child(4) {
  bottom: -100%;
  left: 0;
  width: 2px;
  height: 100%;
  background: linear-gradient(360deg, transparent, #03e9f4);
  animation: animate4 1s linear infinite;
  animation-delay: 0.75s;
}
@keyframes animate4 {
  0% {
    bottom: -100%;
  }
  50%,
  100% {
    bottom: 100%;
  }
}

/* panels */
.mainPanel {
  display: flex;
  flex-direction: row;
  background: #1e181e;
  border: 1px solid #9c3230;
  clip-path: polygon(
    0 0,
    100% 0,
    100% calc(100% - var(--ui-notch-amount) + 2px),
    calc(100% - var(--ui-notch-amount) + 2px) 100%,
    0 100% /* experiment below */ /* 5% 100%,
    0% 80% */
  );
  padding: 0.5em;
  width: 50vw;
  /* max-width: 80%; */
  justify-content: space-between;
}
.mainPanel::before {
  background-color: #9c3230;
  bottom: 5px;
  content: "";
  display: block;
  height: 3px; /* bigger to compensate transform */
  position: absolute;
  right: -6px;
  top: auto;
  transform: rotate(-45deg);
  width: 22.627416px;
  z-index: 100;
}

.leftSideOfPanel {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
}

.rightSideOfPanel {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-end;
}

/* add and cancel buttons */
:root {
  --base-color: #5b13ec;
  --hover-color: cyan;
}

/* label */
.formLabel {
  background-color: var(--colors-primary--600);
  color: var(--colors-on_primary--500);
  font-family: var(--fonts-secondary);
  font-family: "Orbitron";
  font-size: 1.42rem;
  /* font-size: 0.92rem; */
  display: inline-block;
  padding: 0.125em 0.75em;
  padding-right: 2em;
  letter-spacing: 0.065em;
  line-height: 1;
  text-transform: uppercase;
  vertical-align: bottom; /* inline-block fix */
  clip-path: polygon(
    0.25em 0,
    calc(100% - 1em) 0,
    100% 100%,
    0.33em 100%,
    0 100%,
    0 0.25em
  );
  margin-top: 1em;
}

/* name input */
.cyberInput {
