  font-size: 1rem;
  letter-spacing: 0.045em;
  outline: none;
  padding: 0.5rem;
  resize: none;
  /* width: 100%; */
}

.cyberName {
  /* background-color: #3c181a; */
  background-color: #1e181e;
  border: 0;
  box-shadow: none;
  color: #e8615a;
  font-family: "Orbitron";
  font-size: 1rem;
  letter-spacing: 0.045em;
  outline: none;
  padding: 0.5rem;
