  /* width: 100%; */
  color: transparent;
  text-shadow: 0 0 0 #e8615a;
}

.mainLineText {
  color: white;
}

.valueOfStat {
  color: white;
}

/* add/undo buttons */

.addHorizontal {
  outline: none;
  font-family: "Orbitron";
  letter-spacing: 0.02rem;
  cursor: pointer;
  background: transparent;
  border: 0.3rem solid currentColor;
  padding: 0.5rem 0.5rem;
  /* font-size: 2.2rem; */
  color: #2be4ea;
  position: relative;
  transition: color 0.3s;
  margin-bottom: 0.2rem;
  margin-right: 0.2rem;
}
.addHorizontal:hover {
  color: rgb(0, 211, 102);
}
.addHorizontal:hover::before {
  width: 0;
}
.addHorizontal:hover::after {
  height: 0;
}
.addHorizontal:active {
  /* border-width: 0.25rem; */
}
.addHorizontal span {
  position: relative;
  z-index: 2;
}
.addHorizontal::before,
.addHorizontal::after {
  content: "";
  position: absolute;
  background: #1e181e;
  z-index: 1;
  transition: all 0.3s;
}
.addHorizontal::before {
  width: calc(100% - 1.3rem);
  height: calc(101% + 0.7rem);
  top: -0.3rem;
  left: 50%;
  transform: translateX(-50%);
}
.addHorizontal::after {
  height: calc(100% - 1.3rem);
  width: calc(101% + 0.7rem);
  left: -0.3rem;
  top: 50%;
  transform: translateY(-50%);
}

.cancelVertical {
  outline: none;
  font-family: "Orbitron";
  letter-spacing: 0.02rem;
  cursor: pointer;
  background: transparent;
  border: 0.3rem solid currentColor;
  padding: 0.5rem 0.5rem;
  /* font-size: 2.2rem; */
  color: #e8615a;
  position: relative;
  transition: color 0.3s;
  margin-bottom: 0.2rem;
  margin-right: 0.2rem;
}
.cancelVertical:hover {
  color: #e03128;
}
.cancelVertical:hover::before {
  width: 0;
}
.cancelVertical:hover::after {
  height: 0;
}
.cancelVertical:active {
  /* border-width: 0.25rem; */
}
.cancelVertical span {
  position: relative;
  z-index: 2;
}
.cancelVertical::before,
.cancelVertical::after {
  content: "";
  position: absolute;
  background: #1e181e;
  z-index: 1;
  transition: all 0.3s;
}
.cancelVertical::before {
  width: calc(100% - 1.3rem);
  height: calc(101% + 0.7rem);
  top: -0.3rem;
  left: 50%;
  transform: translateX(-50%);
}
.cancelVertical::after {
  height: calc(100% - 1.3rem);
  width: calc(101% + 1rem);
  left: -0.5rem;
  top: 50%;
  transform: translateY(-50%);
}

/* disabled buttons */
.cancelVertical:disabled {
  color: grey;
}

.addHorizontal:disabled {
  color: grey;
}

.cancelVertical:disabled::before {
  width: 0;
}
.cancelVertical:disabled::after {
  height: 0;
}

.addHorizontal:disabled::before {
  width: 0;
}
.addHorizontal:disabled::after {
  height: 0;
}
